
import { defineComponent, reactive, toRefs } from "vue";
import Sakura from "@/components/Sacura.vue";

export default defineComponent({
  components: { Sakura },
  setup() {
    const state = reactive({
      count: 1 as number,
    });
    state.count = state.count + 1;
    return {
      ...toRefs(state),
    };
  },
});
